.Welcomecont{
	font-family: Veranda, sans-serif;
	font-size: 50px;
	color: white;
	text-align: center;
	background-color: rgb(71, 90, 110);
	padding: 0px 0px 0px 0px;
	position: relative;
}

.Welcomecontent{
	height: 1180px;
	width: 100%;
	overflow: hidden;
}

img{
	width: 2500px;
}

.Welcomewords{
	list-style-type: none;
	margin: 435px 0px 0px 590px;
	position: absolute;
	transition: ease 0.3s;
}

.listitem{
	display: inline-block;
}

.letone{
	animation-name: floatingone;
	animation-duration: 3.3s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px -150px;
}

.letone:hover{
	font-size: 300px;
	margin: -100px 0px 0px -290px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

.lettwo{
	animation-name: floatingtwo;
	animation-duration: 3.1s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px 150px;
}

.lettwo:hover{
	font-size: 300px;
	margin: -100px 0px 0px 30px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

.letthr{
	animation-name: floatingthr;
	animation-duration: 3.5s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px 150px;
}

.letthr:hover{
	font-size: 300px;
	margin: -100px 0px 0px 50px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

.letfou{
	animation-name: floatingfou;
	animation-duration: 3.2s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px 150px;
}

.letfou:hover{
	font-size: 300px;
	margin: -100px 0px 0px 50px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

.letsix{
	animation-name: floatingsix;
	animation-duration: 3.6s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px 340px;
}

.letsix:hover{
	font-size: 300px;
	margin: -100px 0px 0px 250px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

.letsev{
	animation-name: floatingsev;
	animation-duration: 3.3s;
	animation-iteration-count: infinite;
	animation-timing-function: ease-in-out;
	margin: 0px 0px 0px 150px;
}

.letsev:hover{
	font-size: 300px;
	margin: -100px 0px 0px 50px;
	transition: font-size ease 1.0s, margin ease 1.0s;
}

@keyframes floatingone{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 50px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingtwo{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 45px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingthr{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 30px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingfou{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 40px); }
	100% { transform: translate(0, -0px); }
}

@keyframes floatingsix{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 50px); }
	100% { transform: translate(0, -0px); }
}
@keyframes floatingsev{
	0% { transform: translate(0, 0px); }
	50% { transform: translate(0, 45px); }
	100% { transform: translate(0, -0px); }
}