.Projectoneclose{
	height: 0px;
	overflow: hidden;
	transition: height ease 0.3s;
}

.Projectoneshow{
	height: 1100px;
	overflow: auto;
	transition: height ease 0.4s;
}

.Projectonetitle{
	font-size: 25px;
	background-color: rgb(91, 114, 138);
	padding: 15px 0px 15px 50px;;

}
.Projectonetitle:hover{
	background-color: rgb(111, 139, 168);
}

.projfiwords{
	font-size: 25px;
	padding: 10px 110px 10px 90px;
}

.piclistfi{
	list-style-type: none;
}

.picsfi{
	display: inline-block;
	padding: 10px;
}

.landingpageport{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	position: relative;
	margin-left: 100px;
	width: 400px;
}

.projectpageport{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	position: relative;
	margin-left: 80px;
	width: 400px;
}

.landingpageport:hover{
	transition: width ease 0.3s;
	width: 900px;
}

.projectpageport:hover{
	transition: width ease 0.3s;
	width: 900px;
}