html{
	scroll-behavior: smooth;
}

body{
	margin: 0;
}
.navWrap{
	z-index: 100;
	position: fixed;
	background-color:rgb(106, 135, 163);
	border-radius: 0px 15px 15px 0px;
	margin: 100px 0px 0px -175px;
	width: 220px;
	padding: 0px 10px 0px 0px;
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	transition: ease 0.3s;
}
.navWrap:hover{
	margin-left: 0px;
}
.navBar{
	list-style-type: none;
	font-family: Veranda, sans-serif;
	font-size: 30px;
}

.navlin{
	background-color:rgb(88, 111, 133);
	font-size: 25px;
	font-weight: 60;
	color: white;
	border-radius: 5px;
	margin: 5px 10px 5px -50px;
	padding: 15px 0px 15px 50px;
	transition: 0.1s;
}

.navlin:hover{
	background-color: rgb(71, 90, 110);
	font-size: 27px;
	border-radius: 5px;
}

a:link{
	color: white;
	text-decoration: none;
}
a:visited{
	color: white;
}
a:active{
	color: grey;
}
