.Projectfourclose{
	height: 0px;
	overflow: hidden;
	transition: height ease 0.3s;
}

.Projectfourshow{
	height: 1800px;
	overflow: auto;
	transition: height ease 0.4s;
}

.Projectfourtitle{
	font-size: 25px;
	background-color: rgb(91, 114, 138);
	padding: 15px 0px 15px 50px;
}

.Projectfourtitle:hover{
	background-color: rgb(111, 139, 168);
}

.projtwowords{
	font-size: 25px;
	padding: 10px 110px 10px 90px;
}

.piclisttwo{
	list-style-type: none;
}

.picstwo{
	display: inline-block;
	padding: 10px;
}

.sitemaptwo:hover, .wireframetwo:hover{
	transition: width ease 0.3s;
	width: 600px;
}

