.Projectfiveclose{
	height: 0px;
	overflow: hidden;
	transition: height ease 0.3s;
}

.Projectfiveshow{
	height: 1500px;
	overflow: auto;
	transition: height ease 0.4s;
}

.Projectfivetitle{
	font-size: 25px;
	background-color: rgb(91, 114, 138);
	padding: 15px 0px 15px 50px;
}

.Projectfivetitle:hover{
	background-color: rgb(111, 139, 168);
}

.projonewords{
	font-size: 25px;
	padding: 10px 110px 10px 90px;
}

.piclistone{
	list-style-type: none;
}

.picsone{
	display: inline-block;
	padding: 10px;
}

.wireframeone:hover{
	transition: width ease 0.3s;
	width: 600px;
}

.sitemapone:hover, .landingpageone:hover, .homepageone:hover, .articlesone:hover, .linksone:hover{
	transition: width ease 0.3s;
	width: 800px;
}

.wireframeone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 100px;
	width: 300px;
}
.sitemapone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 200px;
	width: 400px;
}
.landingpageone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 100px;
	width: 400px;
}
.homepageone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 150px;
	width: 350px;
}
.articlesone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 100px;
	margin-bottom: 50px;
	width: 400px;
}
.linksone{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-left: 150px;
	margin-bottom: 50px;
	width: 400px;
}