.Projectthreeclose{
	height: 0px;
	overflow: hidden;
	transition: height ease 0.3s;
}

.Projectthreeshow{
	height: 1130px;
	overflow: auto;
	transition: height ease 0.4s;
}

.Projectthreetitle{
	font-size: 25px;
	background-color: rgb(91, 114, 138);
	padding: 15px 0px 15px 50px;
}

.Projectthreetitle:hover{
	background-color: rgb(111, 139, 168);
}

.projthrwords{
	font-size: 25px;
	padding: 10px 110px 10px 90px;
}

.mediadeckimage{
	box-shadow: -5px 8px 10px rgb(58, 70, 84);
	margin-top: 50px;
	margin-left: 200px;
	width: 1100px;
}

.mediadeckimage:hover{
	transition: width ease 0.3s, margin ease 0.3s;
	margin: 50px 0px 0px 50px;
	width: 1500px;
}

